.CarApp {
    background-color: ghostwhite;
    text-align: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    width: 60%;
    max-width: 800px;
    margin-left: 20%;
    margin-right: auto;
    margin-top: 2em;
    border: 2px solid black;
    -webkit-box-shadow: 10px 10px 30px 5px rgba(0,0,0,0.9);
            box-shadow: 10px 10px 30px 5px rgba(0,0,0,0.9);
    border-radius: 15px;
    padding-top: 15px;
    
}

.CarApp ul {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    text-align: center;
}

.CarApp li {
    display: inline-block;
    margin: 20px;
}

.CarApp a {
    text-decoration: none;
    color: black;
    padding: 1em;
    
}

.CarApp a:hover,
.CarApp a:active,
.CarApp a.active{
    color: darkorange;
}

p {
    padding: .5em;
}

.confirmation {
    display: grid;
    grid-auto-flow: column;
    height: 6em;
}

@media (max-width: 800px) {
    .CarApp {
        width: 100%;
        margin: 0;
        font-size: .7em;
        border-radius: 0;
        padding-top: 0;

    }
}


